.button {
  @include link;
  display: block;
  padding: 2px 14px;
  text-align: center;
  border-radius: 5px;
  background-color: #e45028;

  &:hover {
    background-color: #c1401e;
  }

  &:active {
    color: rgba($color: #FFFFFF, $alpha: 0.5);
  }

  &--grey {
    background-color: $dark-grey;

    &:hover {
      background-color: #1f2122;
    }
  }
}
